import React from "react";

const Form = ({addContact, newName, newPhone, handleNameChange, handlePhoneChange}) => {
    return (
        <form onSubmit={addContact}>
            <div>
                name: 
                <input
                    value={newName}
                    onChange={handleNameChange}
                />
            </div>
            <div>
                phone number: 
                <input 
                    value={newPhone}
                    onChange={handlePhoneChange}
                />
            </div>
            <div>
                <button type="submit">lisää</button>
            </div>
        </form>
    )
}

export default Form