import React from 'react';
import Name from './components/Name';
import Form from './components/Form';
import personsServices from './services/persons';

class App extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      persons: [],
      newName: '...',
      newPhone: '...'
    }
  }

  componentDidMount() {
    personsServices
      .getAll()
      .then(response => {
        this.setState({persons: response.data})
      });
  }

  addContact = (event) => {
    if (
      this.state.persons.some((item) => item.name === this.state.newName) ||
      this.state.persons.some((item) => item.phone === this.state.newPhone)
      ) {
      alert ("Attention! This name is already in the phonebook!")
    } else {
      event.preventDefault()
      const contactObject = {
        name: this.state.newName,
        phone: this.state.newPhone
      }

      personsServices
        .create(contactObject)
        .then(response => {
          this.setState({
            persons: this.state.persons.concat(response.data),
            newName: "...",
            newPhone: "..."
          })
        })
    }
  }

  handleNameChange = (event) => {
    this.setState({
      newName: event.target.value
    })
  }

  handlePhoneChange = (event) => {
    this.setState({
      newPhone: event.target.value
    })
  }

  deleteContactOf = (id) => {
    return () => {
      const contact = this.state.persons.find(item => item.id === id)

      personsServices
        .remove(id, contact)
        .then(response => {
          this.setState({
            persons: this.state.persons.filter(item => item.id !== id ? item : response.data)
          })
        })
      // axios
      //   .delete(url, contact)
      //   .then(response => {
      //     this.setState({
      //       persons: this.state.persons.map(note => note.id !== id ? note : response.data)
      //     })
      //   })
    }
  }

  render() {
    return (
      <div>
        <h2>Puhelinluettelo</h2>
        <Form 
          newName={this.state.newName}
          newPhone={this.state.newPhone}
          addContact={this.addContact}
          handleNameChange={this.handleNameChange}
          handlePhoneChange={this.handlePhoneChange}
        />
        <h2>Numerot</h2>
        {/* {this.state.persons
          .map(item =>
            <Name
              key={item.name}
              persons={item}
              deleteContact={this.deleteContactOf(item.id)}
            />
          )
        } */}
      </div>
    )
  }
}

export default App